'use client'

import { useState, Suspense } from 'react'
import Link from 'next/link'
import { useRouter, useSearchParams } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Eye, EyeOff, Loader2, CheckCircle2, Sparkles, ArrowRight } from 'lucide-react'
import Image from 'next/image'
import { toast } from 'sonner'

function SignupForm() {
  const [formData, setFormData] = useState({
    instituteName: '',
    adminName: '',
    email: '',
    password: '',
    confirmPassword: '',
    phone: '',
    address: ''
  })
  const [showPassword, setShowPassword] = useState(false)
  const [showConfirmPassword, setShowConfirmPassword] = useState(false)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState(false)
  
  const router = useRouter()
  const searchParams = useSearchParams()
  const selectedPlan = searchParams.get('plan') || 'basic'

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    })
  }

  const handleSignup = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError('')

    if (formData.password !== formData.confirmPassword) {
      setError('Passwords do not match')
      toast.error('Validation Error', { description: 'Passwords do not match' })
      setLoading(false)
      return
    }

    if (formData.password.length < 6) {
      setError('Password must be at least 6 characters long')
      toast.error('Validation Error', { description: 'Password must be at least 6 characters' })
      setLoading(false)
      return
    }

    try {
      const response = await fetch('/api/auth/signup', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          instituteName: formData.instituteName,
          adminName: formData.adminName,
          email: formData.email,
          password: formData.password,
          phone: formData.phone,
          address: formData.address,
          selectedPlan
        })
      })

      const data = await response.json()

      if (!response.ok) {
        setError(data.error || 'Signup failed')
        toast.error('Signup failed', { description: data.error })
        return
      }

      toast.success('Account created!', { description: 'You can now sign in.' })
      setSuccess(true)
    } catch (err) {
      setError('An unexpected error occurred')
      toast.error('Error', { description: 'An unexpected error occurred' })
    } finally {
      setLoading(false)
    }
  }

  if (success) {
    return (
      <div className="min-h-screen flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8 relative overflow-hidden">
        <div className="absolute inset-0 overflow-hidden">
          <div className="blob blob-1 absolute w-96 h-96 -top-48 -left-48" />
          <div className="blob blob-2 absolute w-80 h-80 bottom-0 right-0" />
        </div>
        <div className="absolute inset-0 bg-grid opacity-30" />

        <div className="max-w-md w-full relative z-10">
          <Card className="glass-card shadow-xl animate-scale-in">
            <CardHeader className="text-center pb-6">
              <div className="mx-auto flex items-center justify-center h-16 w-16 rounded-full bg-gradient-to-br from-secondary to-emerald-600 mb-6 shadow-lg animate-bounce-soft">
                <CheckCircle2 className="h-8 w-8 text-white" />
              </div>
              <CardTitle className="text-3xl font-heading">Welcome to InstituteTool!</CardTitle>
              <CardDescription className="text-lg mt-2">
                Your institute account has been created successfully
              </CardDescription>
            </CardHeader>
            <CardContent className="text-center space-y-6">
              <div className="glass p-4 rounded-xl">
                <p className="text-sm text-muted-foreground">
                  Your account is ready! You can now sign in with<br />
                  <strong className="font-semibold text-foreground">{formData.email}</strong>
                </p>
              </div>
              
              <div className="space-y-3">
                <Button size="lg" className="w-full group" asChild>
                  <Link href="/auth/login">
                    Continue to Sign In
                    <ArrowRight className="ml-2 h-5 w-5 group-hover:translate-x-1 transition-transform" />
                  </Link>
                </Button>
                <Button variant="outline" size="lg" className="w-full glass-button" asChild>
                  <Link href="/">Back to Home</Link>
                </Button>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8 relative overflow-hidden">
      <div className="absolute inset-0 overflow-hidden">
        <div className="blob blob-1 absolute w-96 h-96 -top-48 -left-48" />
        <div className="blob blob-2 absolute w-80 h-80 bottom-0 right-0" />
        <div className="blob blob-3 absolute w-72 h-72 top-1/3 right-1/4" />
      </div>
      <div className="absolute inset-0 bg-grid opacity-30" />

      <div className="max-w-lg w-full space-y-8 relative z-10">
        <div className="text-center animate-slide-down">
          <div className="flex justify-center mb-6">
            <div className="w-20 h-20 rounded-2xl flex items-center justify-center shadow-lg hover-lift overflow-hidden">
              <Image 
                src="/logo.svg" 
                alt="InstituteTool Logo" 
                width={80} 
                height={80}
                className="object-contain"
                priority
              />
            </div>
          </div>
          <h1 className="font-heading text-4xl font-bold mb-2">
            Create Your Institute
          </h1>
          <p className="text-muted-foreground text-lg">
            Join thousands of educational institutions
          </p>
          <div className="inline-flex items-center gap-2 glass-card px-4 py-2 text-sm font-semibold text-secondary mt-4">
            <Sparkles className="h-4 w-4 icon-pulse" />
            Selected Plan: <span className="capitalize font-bold">{selectedPlan}</span>
          </div>
        </div>

        <Card className="glass-card shadow-xl animate-slide-up">
          <CardHeader className="space-y-1 pb-6">
            <CardTitle className="text-2xl font-heading text-center">Institute Registration</CardTitle>
            <CardDescription className="text-center">
              Fill in your institute details to get started
            </CardDescription>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSignup} className="space-y-6">
              {error && (
                <div className="glass bg-destructive/10 border-l-4 border-destructive text-destructive px-4 py-3 rounded-lg text-sm animate-scale-in">
                  <p className="text-sm">{error}</p>
                </div>
              )}

              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-2">
                  <label htmlFor="instituteName" className="block text-sm font-semibold">
                    Institute Name *
                  </label>
                  <Input
                    id="instituteName"
                    name="instituteName"
                    type="text"
                    required
                    value={formData.instituteName}
                    onChange={handleInputChange}
                    placeholder="e.g., Excellence Academy"
                    className="glass-input"
                  />
                </div>

                <div className="space-y-2">
                  <label htmlFor="adminName" className="block text-sm font-semibold">
                    Admin Name *
                  </label>
                  <Input
                    id="adminName"
                    name="adminName"
                    type="text"
                    required
                    value={formData.adminName}
                    onChange={handleInputChange}
                    placeholder="Your full name"
                    className="glass-input"
                  />
                </div>
              </div>

              <div className="space-y-2">
                <label htmlFor="email" className="block text-sm font-semibold">
                  Email Address *
                </label>
                <Input
                  id="email"
                  name="email"
                  type="email"
                  autoComplete="email"
                  required
                  value={formData.email}
                  onChange={handleInputChange}
                  placeholder="admin@yourschool.com"
                  className="glass-input"
                />
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-2">
                  <label htmlFor="phone" className="block text-sm font-semibold">
                    Phone Number
                  </label>
                  <Input
                    id="phone"
                    name="phone"
                    type="tel"
                    value={formData.phone}
                    onChange={handleInputChange}
                    placeholder="+1 (555) 123-4567"
                    className="glass-input"
                  />
                </div>

                <div className="space-y-2">
                  <label htmlFor="address" className="block text-sm font-semibold">
                    Institute Address
                  </label>
                  <Input
                    id="address"
                    name="address"
                    type="text"
                    value={formData.address}
                    onChange={handleInputChange}
                    placeholder="Full address"
                    className="glass-input"
                  />
                </div>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-2">
                  <label htmlFor="password" className="block text-sm font-semibold">
                    Password *
                  </label>
                  <div className="relative">
                    <Input
                      id="password"
                      name="password"
                      type={showPassword ? 'text' : 'password'}
                      autoComplete="new-password"
                      required
                      value={formData.password}
                      onChange={handleInputChange}
                      placeholder="Create a strong password"
                      className="glass-input pr-12"
                    />
                    <button
                      type="button"
                      className="absolute inset-y-0 right-0 pr-4 flex items-center text-muted-foreground hover:text-foreground transition-colors"
                      onClick={() => setShowPassword(!showPassword)}
                    >
                      {showPassword ? <EyeOff className="h-5 w-5" /> : <Eye className="h-5 w-5" />}
                    </button>
                  </div>
                </div>

                <div className="space-y-2">
                  <label htmlFor="confirmPassword" className="block text-sm font-semibold">
                    Confirm Password *
                  </label>
                  <div className="relative">
                    <Input
                      id="confirmPassword"
                      name="confirmPassword"
                      type={showConfirmPassword ? 'text' : 'password'}
                      autoComplete="new-password"
                      required
                      value={formData.confirmPassword}
                      onChange={handleInputChange}
                      placeholder="Confirm your password"
                      className="glass-input pr-12"
                    />
                    <button
                      type="button"
                      className="absolute inset-y-0 right-0 pr-4 flex items-center text-muted-foreground hover:text-foreground transition-colors"
                      onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                    >
                      {showConfirmPassword ? <EyeOff className="h-5 w-5" /> : <Eye className="h-5 w-5" />}
                    </button>
                  </div>
                </div>
              </div>

              <Button type="submit" disabled={loading} className="w-full group" size="lg">
                {loading ? (
                  <>
                    <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                    Creating your institute...
                  </>
                ) : (
                  <>
                    <Sparkles className="mr-2 h-5 w-5 group-hover:animate-pulse" />
                    Create Institute Account
                  </>
                )}
              </Button>

              <p className="text-xs text-muted-foreground text-center leading-relaxed">
                By creating an account, you agree to our{' '}
                <Link href="/terms" className="text-primary hover:underline font-medium">
                  Terms of Service
                </Link>{' '}
                and{' '}
                <Link href="/privacy" className="text-primary hover:underline font-medium">
                  Privacy Policy
                </Link>
              </p>
            </form>
          </CardContent>
        </Card>

        <div className="text-center animate-slide-up stagger-3">
          <p className="text-sm text-muted-foreground">
            Already have an account?{' '}
            <Link href="/auth/login" className="font-semibold text-primary hover:text-primary/80 transition-colors">
              Sign in here
            </Link>
          </p>
        </div>
      </div>
    </div>
  )
}

function SignupLoading() {
  return (
    <div className="min-h-screen flex items-center justify-center relative overflow-hidden">
      <div className="absolute inset-0 overflow-hidden">
        <div className="blob blob-1 absolute w-96 h-96 -top-48 -left-48" />
        <div className="blob blob-2 absolute w-80 h-80 bottom-0 right-0" />
      </div>
      <div className="animate-pulse relative z-10">
        <div className="w-16 h-16 bg-muted rounded-2xl mx-auto mb-4"></div>
        <div className="h-8 w-48 bg-muted rounded mx-auto"></div>
      </div>
    </div>
  )
}

export default function SignupPage() {
  return (
    <Suspense fallback={<SignupLoading />}>
      <SignupForm />
    </Suspense>
  )
}
